  }
  .explanation__tool::before {
    width: 80px;
    height: 80px;
    background-repeat: no-repeat;
  }
  .explanation__tool::after {
    font-family: 'Oswald', Arial, sans-serif;
    font-weight: normal;
    font-size: 280px;
    line-height: 280px;
    position: absolute;
    top: -39px;
    color: #f2f2f2;
    z-index: -1;
  }
  .explanation__tool--substances::after {
    content: "1";
    left: 118px;
  }
  .explanation__tool--cooking::after {
    content: "2";
    left: 111px;
  }
  .explanation__tool--eating::after {
    content: "3";
    left: 113px;
  }
  .explanation__tool--time::after {
    content: "4";
